.text-white {
    color: #fff!important;
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.title-Accueil{
    font-size: 4.5em;
    font-weight: 600;
    line-height: 1.2;
}
.text-secondary{
    margin-bottom: 10px !important;
}
.welcome-area {
    background: rgba(0, 0, 0, 0) url('./Img/blog-4.jpg') no-repeat scroll center center / cover;
    height: 800px;
    z-index: 1;
}
.button-group {
    margin-top: 30px;
}
.btn {
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    line-height: 1;
    text-align: center;
    padding: 18px 30px;
    border: 0 none;
    border-radius: 6px;
    outline: 0 none;
    position: relative;
    z-index: 1;
    background: linear-gradient(
-47deg
, #8731E8 0%, #4528DC 100%);
}
.btn.sApp-btn {
    background: transparent none repeat scroll 0 0;
    color: #444;
    border-radius: 100px;
    letter-spacing: 1px;
}

.btn.sApp-btn:after {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    border-radius: 100px;
    background: #fff none repeat scroll 0 0;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    z-index: -1;
}
.btn.sApp-btn:before {
    position: absolute;
    content: "";
    height: calc(100% + 4px);
    width: calc(100% + 4px);
    top: -2px;
    left: -2px;
    border-radius: 100px;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    z-index: -2;
}
.bg-overlay, .overlay-dark {
    position: relative;
   
}
.welcome-area.bg-overlay::after {
    z-index: -2;
}
.bg-overlay::after, .benifits-item:after {
    background: linear-gradient(
-47deg
, #8731E8 0%, #4528DC 100%);
    background-image: linear-gradient(
-47deg
, rgb(135, 49, 232) 0%, rgb(69, 40, 220) 100%);
opacity: 0.94;
position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
.my-4 {
    margin-bottom: 1.5rem!important;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
}

.button-group a:last-child {
    margin-right: 0;
}

 
.store-buttons a {
    text-align: left;
}




.contact-box button {
    height: 60px;
    border-radius: 8px;
    background: #7c4fe0 !important;
}

.contact-bottom > span {
    font-size: 13px;
}

.mt-3, .my-3 {
    margin-top: 1rem!important;
}








.text-center {
    text-align: center!important;
}
form {
    display: block;
    margin-top: 0em;
}
.contact-title{
    font-size: 2em;
    line-height: 1.2;
    font-weight: 600;
    color: #222;
    margin-bottom: 0;

}
 

.text-secondary {
    color: #666 !important;
}
.fw-3 {
    line-height: 1.4;
    font-weight: 300;
}
.welcome-area .shape-bottom {
    z-index: -1;
}

.shape-bottom {
    position: absolute;
    top: auto;
    bottom: -1px;
    left: 0;
    right: 0;
}
.shape-bottom svg {
    overflow: hidden;
    width: 100%;
    line-height: 0;
    direction: ltr;
    transform: rotate(
180deg
);
}

svg {
    overflow: hidden;
    vertical-align: middle;
}
/*responsive*/
 
@media (min-width: 992px){
    .mt-lg-0, .my-lg-0 {
        margin-top: 0!important;
    }
    
    .col-lg-5 {
        -ms-flex: 0 0 41.666667%;
        flex: 0 0 41.666667%;
        max-width: 41.666667%;
    }
}

@media (min-width: 768px){
    
    .col-md-8 {
        -ms-flex: 0 0 66.666667%;
        flex: 0 0 66.666667%;
        max-width: 66.666667%;
    }
}


@media (min-width: 576px){

   .p-sm-5 {
       padding: 2rem!important;
   }
   .p-4 {
       padding: 1.5rem!important;
   }
   .mt-5, .my-5 {
       margin-top: 3rem!important;
   }
   .shadow-lg {
       box-shadow: 0 1rem 3rem rgba(0,0,0,.175)!important;
   }
   .rounded {
       border-radius: .25rem!important;
   }
   .bg-white {
       background-color: #fff!important;
   }
   
   
}
@media (max-width: 425px){

   #Home{
     
       padding-bottom:600px;
       padding-top:500px
   }
  
   .title-Accueil{
       font-size: 2rem;
   }
 
    
